import {
  FilterTasks,
  FilterTasksAndCalendarPanel,
  Title,
  WrapCalendar,
  Wrapper,
} from "./styles-calendar";
import UnfulfilledTasks from "./unfulfilled-tasks/unfulfilled-tasks";
import PerformedTasks from "./performed-tasks/performed-tasks";
import filterIcon from "@assets/images/calendar/filter.svg";
import { DatePickCalendar, GeneralContainer } from "@components/common";
import BigCalendar from "react-big-calendar";
import moment from "moment";
import Year from "./year";
import "react-big-calendar/lib/css/react-big-calendar.css";
import RBCToolbar from "./calendar-toolbar/calendar-toolbar";
import { useState } from "react";
const Calendar = () => {
  const localizer = BigCalendar.momentLocalizer(moment);
  const [choseTime,setChoseTime]= useState('')
  const [selectedDate,setSelectedDate]= useState('')
  localizer.formats.yearHeaderFormat = "YYYY";
  // console.log(new Date());
  const events = [
    {
      id: 0,
      title: "All",
      start: new Date("Wed Jun 01 2022 10:03:16 GMT+0500 "),
      end: new Date(),
    },
  ];
  console.log(choseTime);
  return (
    <GeneralContainer>
      <Wrapper>
        <WrapCalendar>
          <FilterTasksAndCalendarPanel>
            <DatePickCalendar setChoseTime={setChoseTime}/>
            <FilterTasks>
              <Title>
                <img src={filterIcon} alt="icon" />
                <h2>Filter</h2>
              </Title>
              <PerformedTasks />
              <UnfulfilledTasks />
            </FilterTasks>
          </FilterTasksAndCalendarPanel>
          <BigCalendar
            localizer={localizer}
            events={events}
            toolbar={true}
            culture="uz-UZ"
            views={{
              day: true,
              week: true,
              month: true,
              year: Year,
            }}
            components={{
              toolbar: props => ( <RBCToolbar {...props} choseTime={choseTime} /> )
            }}
            messages={{ year: "Year" }}
          />
        </WrapCalendar>
      </Wrapper>
    </GeneralContainer>
  );
};
export default Calendar;
