import styled from "styled-components";

export const Wrapper = styled.div`
  width: 100%;
  margin: 10px 0;
  display: flex;
  justify-content: space-between;
  padding: 11px 10px;
  border-radius: 7px;
  background: #444;
  color: #fff;
  h4 {
    font-size: 12px;
    font-weight: 500;
  }
  p {
    font-size: 12px;
    font-weight: 600;
  }
`;
