import moment from "moment";
import { useState } from "react";
import calendarImg from "@assets/images/calendar/calendar.svg";
import plusImg from "@assets/images/calendar/plus-white.svg";
import { DropDownMenu, SearchInput, UniversalModal } from "@components/common";
import notification from "@assets/images/dashboard/dashboard-navbar/Notification.svg";
import two_user from "@assets/images/dashboard/two-user.svg";
import three_user from "@assets/images/dashboard/three-user.svg";
import accept from "@assets/images/dashboard/accept.svg";
import CreateNote from "@components/common/create-note/create-note";

function RBCToolbar(props) {
  console.log(props);
  const [viewState, setViewState] = useState("month");
  const [openCreateNote, setOpenCreateNote] = useState(false);
  //   const [StudyTimechecked, setStudyTimechecked] = useState(false)
  const getCustomToolbar = () => {
    // const toolbarDate = props.date;
    const goToDayView = () => {
      props.onView("day");
      setViewState("day");
    };
    const goToWeekView = () => {
      props.onView("week");
      setViewState("week");
    };
    const goToMonthView = () => {
      props.onView("month");
      setViewState("month");
    };
    const goToYearView = () => {
      props.onView("year");
      setViewState("year");
    };
    const goToBack = () => {
      let view = viewState;
      let mDate = props.date;
      let newDate;
      if (view === "month") {
        newDate = new Date(mDate.getFullYear(), mDate.getMonth() - 1, 1);
      } else if (view === "week") {
        newDate = new Date(
          mDate.getFullYear(),
          mDate.getMonth(),
          mDate.getDate() - 7,
          1
        );
      } else {
        newDate = new Date(
          mDate.getFullYear(),
          mDate.getMonth(),
          mDate.getDate() - 1,
          1
        );
      }
      props.onNavigate("prev", newDate);
    };
    const goToNext = () => {
      let view = viewState;
      let mDate = props.date;
      let newDate;
      if (view === "year") {
        newDate = new Date(mDate.getFullYear() + 12);
      } else if (view === "month") {
        newDate = new Date(mDate.getFullYear(), mDate.getMonth() + 1, 1);
      } else if (view === "week") {
        newDate = new Date(
          mDate.getFullYear(),
          mDate.getMonth(),
          mDate.getDate() + 7,
          1
        );
      } else {
        newDate = new Date(
          mDate.getFullYear(),
          mDate.getMonth(),
          mDate.getDate() + 1,
          1
        );
      }
      props.onNavigate("next", newDate);
    };
    const onSelect = (value) => {
      console.log(value);
value==='Eslatma'&&
setOpenCreateNote(true)
    };

    const goToToday = () => {
      const now = new Date();
      props.date.setMonth(now.getMonth());
      props.date.setYear(now.getFullYear());
      props.date.setDate(now.getDate());
      props.onNavigate("current");
    };
    const addMenu = [
      {
        icon: notification,
        title: "Eslatma",
      },
      {
        icon: accept,
        title: "Vazifa",
      },
      {
        icon: two_user,
        title: "Uchrashuv",
      },
      {
        icon: three_user,
        title: "Majlis",
      },
    ];
    const goToBackYear = () => {
      let mDate = props.date;
      let newDate = new Date(mDate.getFullYear() - 1, 1);
      props.onNavigate("prev", newDate);
    };

    const goToNextYear = () => {
      let mDate = props.date;
      let newDate = new Date(mDate.getFullYear() + 1, 1);
      props.onNavigate("next", newDate);
    };

    const month = () => {
      const date = moment(props.date);
      let month = date.format("MMMM");
      let day = date.format("D");

      return (
        <span>
          <span className="prev-icon" id="prev-btn-icon" onClick={goToBack}>
            &#8249;
          </span>
          <span className="rbc-toolbar-label rbc-date">
            <i className="far fa-calendar"></i> <span>{`${month} ${day}`}</span>
          </span>
          <span className="next-icon" id="next-btn-icon" onClick={goToNext}>
            &#8250;
          </span>
        </span>
      );
    };
    const year = () => {
      const date = moment(props.date);
      let year = date.format("YYYY");

      return (
        <span className="rbc-btn-group">
          {viewState === "month" ||
            (viewState === "year" && (
              <button type="button" onClick={goToBackYear}>
                <span className="prev-icon">&#8249;&#8249;</span>
              </button>
            ))}
          <span className="rbc-toolbar-label">{year}</span>
          {viewState === "month" ||
            (viewState === "year" && (
              <button type="button" onClick={goToNextYear}>
                <span className="prev-icon">&#8250;&#8250;</span>
              </button>
            ))}
        </span>
      );
    };

    const day = () => {
      let view = viewState;
      const date = moment(props.date);
      let day;
      if (view === "day") {
        day = date.format("ddd") + " " + date.format("Do");
      }
      return (
        <span>
          <span className="prev-icon" id="prev-btn-icon" onClick={goToBack}>
            &#8249;
          </span>
          <span className="rbc-toolbar-label">{day}</span>
          <span className="next-icon" id="next-btn-icon" onClick={goToNext}>
            &#8250;
          </span>
        </span>
      );
    };

    return (
      <div className="rbc-toolbar">
         <UniversalModal
          open={openCreateNote}
          handleClose={() => setOpenCreateNote(false)}
        >
        <CreateNote/>
        </UniversalModal>
        <div className="rbc-toolbar-item-2">
          <span className="calendar-title-box">
            <img src={calendarImg} alt="icon" />
            <h1>Kalendar</h1>
          </span>
          <span className="calendar-add-box">
            <DropDownMenu bordered={true} menuData={addMenu} onSelect={onSelect}>
              <button className="add-task-btn">
                <img src={plusImg} alt="icon" />
                <h1>Qo'shish</h1>
              </button>
            </DropDownMenu>
          </span>
          <span className="rbc-btn-group">
            <span className="next-icon today" onClick={goToToday}>
              Today
            </span>
          </span>
          {viewState === "year"
            ? year()
            : viewState === "month" || viewState === "week"
            ? month()
            : day()}
          <span className="rbc-btn-group">
            <button className="" onClick={goToMonthView}>
              <span className="label-filter-off">Month</span>
            </button>
            <button className="" onClick={goToDayView}>
              <span className="label-filter-off">Day</span>
            </button>
            <button className="" onClick={goToWeekView}>
              <span className="label-filter-off">Week</span>
            </button>
            <button className="" onClick={goToYearView}>
              <span className="label-filter-off">Year</span>
            </button>
          </span>
          <span className="serch-box">
            <SearchInput
              placeholder="search"
              name="search"
              onChange={() => console.log("search calendar")}
            />
          </span>
        </div>
      </div>
    );
  };

  return <>{getCustomToolbar()}</>;
}

export default RBCToolbar;
