import styled from "styled-components";

export const Wrapper = styled.div`
  /* background: #fff; */
  border-radius: 7px;
  padding: 30px 0;
  padding-top: 0;
  /* height: 100%; */
  width: 100%;
  margin-bottom: 30px;
  height:calc(100% - 161px);
  h1 {
    font-size: 12px;
    padding: 0;
    margin-bottom: 10px;
    font-weight: 500;
  }
`;
export const Tasks = styled.div`
  /* min-height: 140px; */
  height: calc(100% - 23px);
  overflow-y: scroll;
  padding: 0;
  ::-webkit-scrollbar {
    width: 6px;
  }

  /* Track */
  ::-webkit-scrollbar-track {
    border-radius: 10px;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #a5a9b8;
    border-radius: 10px;
  }

  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #a5a9b8;
  }
`;
