import styled from "styled-components";
export const WrapCalendar = styled.div`
  display: flex;
  justify-content: space-between;
  position: relative;
  .rbc-toolbar {
    background: #fff;
    padding: 6px 17px;
    border-radius: 7px;
  }
  .Cell-dayOfWeek {
    font-size: 17px !important;
  }
  .app {
    width: 100%;
    height: 100%;
  }
  .rbc-calendar {
    width: 100%;
  }
  .rbc-btn-group {
    button {
      border: none;
      color: #71717a;
      font-size: 14px !important;
      font-weight: 500 !important;
    }
  }
  .rbc-active {
    background-color: #0496ff !important;
    color: #fff !important;
    border-radius: 7px !important;
    /* padding: 4px 16px; */
  }
  .rbc-toolbar-label {
    font-size: 16px;
    font-weight: 600;
  }
  .rbc-toolbar button:active {
    box-shadow:none;
  }
  .year {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    background: #fff;
    grid-gap: 5px;
    width: 76%;
    margin-left: auto;
    border-radius: 7px;
    padding: 16px;
    /* flex-wrap: wrap; */
    justify-content: center;
  }

  .month {
    margin: 5px auto 35px auto;
    width: max-content;
  }
  .rbc-btn-group .today {
    color: #000;
    background: none;
    font-size: 12px;
    font-weight: 500;
    cursor: pointer;
    border: 1px solid #00000033;
    padding: 10px 31px;
    border-radius: 7px;
  }
  .serch-box {
    button {
      border: none;
    }
    div {
      max-height: 30px;
    }
    img {
      width: 15px;
      height: 15px;
    }
  }
  .add-task-btn {
    :hover {
      background: #0496ff;
      color: #fff;
    }
    background: #0496ff;
    cursor: pointer;
    color: #fff;
    display: flex;
    align-items: center;
    border-radius: 7px;
    padding: 10px 25px;
    h1 {
      font-size: 16px;
      font-weight: 500;
    }
  }

  .month-name {
    font-size: 12px;
    margin-left: 10px;
    font-weight: 700;
    margin-bottom: 10px;
  }
  .calendar-title-box {
    display: flex;
    align-items: center;
    h1 {
      margin-left: 13px;
      font-size: 20px;
      font-weight: 600;
    }
  }
  .rbc-toolbar-item-2 {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-between;
  }
  .day {
    display: inline-block;
    width: 20px;
    font-size: 12px;
    font-weight: 500;
    margin: 0 7px;
    margin-bottom: 4px;
    height: 20px;
    text-align: center;
    line-height: 30px;
  }
  .calendar-add-box {
    .css-8je8zh-MuiTouchRipple-root,
    .css-8je8zh-MuiTouchRipple-root span {
      display: none;
    }
    button {
      background: #0496ff;
      color: #fff;
      border: none;
    }
    > div > button {
      background: none;
      :hover,
      :active {
        background: none !important;
      }
      border: none !important;
    }
  }
  .rbc-now {
    a {
      background: #0496ff;
      color: #fff;
      border-radius: 25px;
      padding: 5px;
    }
  }
  .rbc-date-cell {
    padding: 5px;
  }
  .date {
    width: 20px;
    height: 20px;
    background: white;
    border-radius: 50px;
    border: none;
    margin: 0 7px;
    outline: none;
  }

  .date.in-month:hover {
    cursor: pointer;
    color: white;
    background: #0496ff;
  }

  .today {
    color: white;
    background: #0496ff;
  }
  .rbc-month-view {
    width: 76%;
    height: 650px;
    margin-left: auto;
    background: #fff;
    padding: 16px;
    border: none;
    border-bottom: 1px solid #ddd;
    border-radius: 7px;
    flex: none;
  }
  .date.prev-month,
  .date.next-month {
    color: grey;
    opacity: 0;
  }
  #next-btn-icon,
  #prev-btn-icon {
    cursor: pointer;
  }
  .rbc-header {
    padding: 30px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    span {
      font-size: 24px;
      font-weight: 500;
    }
  }
  .rbc-today {
    background: none;
  }
  .rbc-time-view {
    width: 76%;
    margin-left: auto;
    background: #fff;
    padding: 16px;
    border-radius: 7px;
    height: 745px;
    border: none;
    border-bottom: 1px solid #ddd;
  }
  .rbc-time-header-cell {
    .rbc-today {
      background: #0496ff;
      color: #fff;
      border-radius: 7px;
    }
  }
  .rbc-event {
    background: #a973d1;
    padding: 4px 24px;
    border-radius: 7px;
    width: 100%;
  }
  .rbc-day-slot .rbc-time-slot {
    border-top: 0;
  }
  .rbc-time-content {
    height: 680px;
    flex: initial;
    overflow-y: auto;
  }
`;
export const Wrapper = styled.div`
  width: 100%;
  /* .css-1ps6pg7-MuiPaper-root {
    padding: 16px;
  }
  .css-4plepo.MainLayout-container,
  .css-1ps6pg7-MuiPaper-root {
    border-radius: 7px;
    background: none;
  } */
`;
export const AboutTasks = styled.div`
  display: flex;
  width: 100%;
  justify-content: space-between;
`;
export const FilterTasksAndCalendarPanel = styled.div`
  min-width: 260px;
  width: 19%;
  position: absolute;
  top: 79px;
  background: #ffffff;
  border-radius: 7px;
  min-height: 650px;
  height: calc(100% - 68px);
`;
export const FilterTasks = styled.div`
  padding: 0 20px;
  margin-top: 20px;
  height: calc(100% - 279px);
`;
export const Title = styled.div`
  display: flex;
  align-items: center;
  h2 {
    font-size: 16px;
    font-weight: 500;
    margin-left: 8px;
  }
`;
