import { FC, useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import {
  AuthorBtn,
  DateInput,
  Input,
  IssuesConsidered,
  LightButton,
  LightSelect,
  MuiSlider,
  PartnerSelection,
  SelectionParticipants,
  Selector,
  StartToFinish,
  UniversalModal,
  UploadFile,
} from "@components/common";
import {
  AvatarImg,
  AvatarImgNone,
  ButtonCard,
  Card,
  DateWrapper,
  FlexBox,
  Form,
  GroupCard,
  IconButton,
  Img,
  InputCard,
  Label,
  RepeatSelectBox,
  SaveButton,
  SelectedStaffs,
  Title,
  TitleRepeat,
  WithWho,
  WithWhoWrap,
  Wrapper,
  WrapRepeatContent,
} from "./styles-create-note";
import { CreateNoteForm, CreateNoteProps } from "./types-create-note";
import profileAvatarIcon from "@assets/images/meetings/profile-avatar.svg";
import people_group from "@assets/images/meetings/people-group.svg";

import plusIcon from "@assets/images/meetings/plus-icon-blue.svg";
import {
  Checkbox,
  FormControl,
  FormControlLabel,
  FormGroup,
  Radio,
  RadioGroup,
} from "@mui/material";
import StatusBtn from "@components/common/status-btn/status-btn";

const CreateNote: FC<CreateNoteProps> = () => {
  const [repeatValue, setRepeatValue] = useState<string>("Bir marta");
  const [notifications, setNotifications] = useState<any>([]);
  const [open, setOpen] = useState<boolean>(false);
  const { handleSubmit, control } = useForm<CreateNoteForm>({
    defaultValues: {
      withWho: "",
      description: "",
      locationName: "",
    },
  });

  const handleClose = () => {
    setOpen(false);
  };
  const onChange = (type: string, value: any) => {
    console.log("type: ", type);
  };
  const weekDays = [
    { day: "Dushanba", value: 0 },
    { day: "Seshanba", value: 1 },
    { day: "Chorshanba", value: 2 },
    { day: "Payshanba", value: 3 },
    { day: "Juma", value: 4 },
    { day: "Shanba", value: 5 },
    { day: "Yakshanba", value: 6 },
  ];
  const handleEdit = (e: number, time: number | number[]) => {
    let sliderNotifications = [...notifications];
    let sliderNotifications1 = { ...notifications[e] };
    sliderNotifications1.time = time;
    sliderNotifications[e] = sliderNotifications1;
    setNotifications(sliderNotifications);
  };
  const handleRemove = async (e: number) => {
    let notif = notifications;
    notif = notif.filter((item: any, idx: number) => idx != e);
    setNotifications(notif);
  };
  return (
    <Wrapper>
      <Title>Eslatma qo'shish</Title>
      {/* <ButtonCard>
        <AuthorBtn />
        <UploadFile />
        <LightSelect />
        <StatusBtn status="new" />
      </ButtonCard> */}
      <Form>
        <GroupCard>
          <InputCard>
            <Input
              name="withWho"
              control={control}
              title=""
              placeholder="Eslatma nomi"
              onChange={onChange}
            />
          </InputCard>
        </GroupCard>
        <GroupCard>
          <InputCard>
            <Input
              name="description"
              forComment
              control={control}
              title=""
              placeholder="Izoh kiriting"
              onChange={onChange}
            />
          </InputCard>
        </GroupCard>
        <Card marginTop="15px">
          <InputCard>
            <DateWrapper>
              <TitleRepeat>Boshlanish vaqti</TitleRepeat>
              <DateInput />
            </DateWrapper>
            <RepeatSelectBox>
              <TitleRepeat>Takroriyligi</TitleRepeat>
              <Selector
                options={[
                  "Bir marta",
                  "Har kuni",
                  "Har hafta",
                  "Har oy",
                  "Har yil",
                ]}
                placeholder=" "
                selectedValue={setRepeatValue}
                marginTop="10px"
                defaultValue="Bir marta"
                withClearIcon
                isAutoComplete={false}
              />
            </RepeatSelectBox>
          </InputCard>
        </Card>
      </Form>
      <Card style={{ alignItems: "start" }} marginTop="10px">
        <WrapRepeatContent>
          {repeatValue !== "Bir marta" && (
            <RepeatSelectBox>
              <TitleRepeat style={{ marginBottom: "10px" }}>
                Oxirgi eslatma vaqti
              </TitleRepeat>
              <DateInput />
            </RepeatSelectBox>
          )}
        </WrapRepeatContent>
        {repeatValue === "Har hafta" ? (
          <FormGroup>
            {weekDays.map((day) => (
              <FormControlLabel
                control={<Checkbox defaultChecked={day.value === 0} />}
                label={day.day}
              />
            ))}
          </FormGroup>
        ) : repeatValue === "Har oy" || repeatValue === "Har yil" ? (
          <FormControl>
            <RadioGroup
              aria-labelledby="demo-radio-buttons-group-label"
              defaultValue="0"
              name="radio-buttons-group"
            >
              <FormControlLabel
                value="0"
                control={<Radio />}
                label="Har oyning 30-sanasida"
              />
              <FormControlLabel
                value="3"
                control={<Radio />}
                label="Har oyning oxirgi sanasida"
              />
              <FormControlLabel
                value="1"
                control={<Radio />}
                label="Har oyning 5-Dushanbasida"
              />
              <FormControlLabel
                value="2"
                control={<Radio />}
                label="Har oyning oxirgi Dushanbasida"
              />
            </RadioGroup>
          </FormControl>
        ) : (
          ""
        )}
        <div style={{ width: "100%" }}>
          <FlexBox>
            <TitleRepeat>Oldindan eslatma</TitleRepeat>
            <img
              src={plusIcon}
              onClick={() =>
                setNotifications([
                  ...notifications,
                  { id: notifications.length, time: 10 },
                ])
              }
              alt="icon"
            />
          </FlexBox>
          {notifications.map((notification: any, idx: number) => (
            <MuiSlider
              key={idx}
              id={idx}
              value={notification.time}
              onEdit={handleEdit}
              onRemove={handleRemove}
            />
          ))}
        </div>
      </Card>
      <SaveButton forSave>Saqlash</SaveButton>
      {/* 
        <Separator width="30px" />
        <Separator width="30px" />
        <LightButton
          icon={<Icon src={statistics_icon} border_radius />}
          text="Status"
        />
        <Separator width="30px" />

      <UniversalModal
        open={open}
        handleClose={handleClose}
        children={<PartnerSelection />}
      /> */}
    </Wrapper>
  );
};

export default CreateNote;
