import { FC } from "react";
import CalendarTask from "../common/calendar-task/calendar-task";
import { Tasks, Wrapper } from "./styles-unfulfilled-tasks";
import { UnfulfilledTasksProps } from "./types-unfulfilled-tasks";

const UnfulfilledTasks: FC<UnfulfilledTasksProps> = () => {
  const data = [
    { title: "Zalda majlisim bor", time: "11:00 - 12:00", type: "task" },
    { title: "Zalda majlisim bor", time: "11:00 - 12:00", type: "note" },
    { title: "Zalda majlisim bor", time: "11:00 - 12:00", type: "note" },
    { title: "Zalda majlisim bor", time: "11:00 - 12:00", type: "note" },
    { title: "Zalda majlisim bor", time: "11:00 - 12:00", type: "note" },
  ];
  return (
    <Wrapper>
      <h1>Kun tartibi</h1>
      <Tasks>
        {data.map((task, idx) => (
          <CalendarTask key={idx} data={task} />
        ))}
      </Tasks>
    </Wrapper>
  );
};

export default UnfulfilledTasks;
