import { FC, useEffect, useState } from "react";
import { Wrapper } from "./styles-calendar-task";
import { CalendarTaskProps } from "./types-calendar-task";

const CalendarTask: FC<CalendarTaskProps> = ({ data }) => {
  const [backgroundColor, setBackgroundColor] = useState("#0496FF");
  useEffect(() => {
    switch (data.type) {
      case "task":
        setBackgroundColor("#06D6A0");
        break;
      case "note":
        setBackgroundColor("#EF476F");
        break;
      case "assembly":
        setBackgroundColor("#A973D1");
        break;
      case "meeting":
        setBackgroundColor("#FCCA58");
        break;
      default:
        setBackgroundColor(backgroundColor);
    }
  }, [data]);
  return (
    <Wrapper style={{ background: backgroundColor }}>
      <h4>{data.title}</h4>
      <p>{data.time}</p>
    </Wrapper>
  );
};

export default CalendarTask;
