import styled from "styled-components";

export const Wrapper = styled.div`
  background: #fff;
  border-radius: 7px;
  max-width: 410px;
  max-height: 348px;
  height: max-content;
  width: 100%;
  margin-bottom: 21px;
  h1 {
    font-size: 20px;
    margin-bottom: 15px;
    font-weight: 500;
  }
  .css-j204z7-MuiFormControlLabel-root {
    margin-left: -3px;
  }
`;
export const Tasks = styled.div`
  /* height: 200px; */
  margin-top: 10px;
  ::-webkit-scrollbar {
    width: 6px;
  }

  /* Track */
  ::-webkit-scrollbar-track {
    border-radius: 10px;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #a5a9b8;
    border-radius: 10px;
  }

  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #a5a9b8;
  }
  .note {
    color: #ef476f !important;
  }
  .task {
    color: #06d6a0 !important;
  }
  .assembly {
    color: #0881d8 !important;
  }
  .meeting {
    color: #f47c39 !important;
  }
  .css-i4bv87-MuiSvgIcon-root {
    width: 27px;
    height: 27px;
  }
  .css-12wnr2w-MuiButtonBase-root-MuiCheckbox-root {
    padding: 0;
    padding-right: 3px;
    padding-bottom: 3px;
  }
  .css-j204z7-MuiFormControlLabel-root .MuiFormControlLabel-label {
    font-size: 13px;
    font-weight: 400;
  }
`;
