import SearchInput from "@components/common/search-input/search-input";
import { FormControlLabel, FormGroup } from "@mui/material";
import Checkbox from "@mui/material/Checkbox";
import { FC } from "react";
import CalendarTask from "../common/calendar-task/calendar-task";
import { Tasks, Wrapper } from "./styles-performed-tasks";
import { PerformedTasksProps } from "./types-performed-tasks";

const PerformedTasks: FC<PerformedTasksProps> = () => {
  const data = [
    { title: "Eslatma", type: "note" },
    { title: "Vazifa", type: "task" },
    { title: "Majlis", type: "assembly" },
    { title: "Uchrashuv", type: "meeting" },
  ];
  return (
    <Wrapper>
      <Tasks>
        <FormGroup>
          {data.map((item, idx) => (
            <FormControlLabel
              key={idx}
              control={<Checkbox className={item.type} defaultChecked />}
              label={item.title}
            />
          ))}
        </FormGroup>
      </Tasks>
    </Wrapper>
  );
};

export default PerformedTasks;
